
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import header from '@/components/header';
import emptyTips from '@/components/emptyTips';
import LingLoading from '@/components/attendance/LingLoading';
export default {
props: {
userId: {
Number,
default: 0
},
userType: {
Number,
default: 0
}
},
data() {
return {
showTips: 0,
mailBoxList: [],
titleContent: '园长邮箱',
defaultAvatar: 'http://cdn-didano.oss-cn-shenzhen.aliyuncs.com/xiaonuo/wx/img/logo_2.jpg',
state: {
isShow: true,
message: '加载中',
state: 'loading',
},
sheetShow: false,
sheetMenus: {
menu1: '确定要删除该消息？',
menu2: '<span style="color:red">删除</span>'
},
deleteMessageId: '',
selectItem: -1,
}
},
created() {
this._initMailBox();
},
methods: {
_initMailBox() {
this.$http.post(`${WX_URL}/base/mailbox/post/mail_findtByBossId/${this.userId}/${this.userType}`).then((response) => {
response = response.data;
if( response.success ) {
this.state.isShow = false;
this.mailBoxList = response.data;
if (!this.mailBoxList.list.length) {
this.showTips = 1;
} else {
this.showTips = 0;
}
}
},(response) => {
this.state.state = 'error';
this.state.message = '网络错误';
});
},

isRead(noticeId,index) {
this.$http.post(`${WX_URL}base/notice/post/setNoticeRead`,{'noticeId':noticeId,'userId':this.userId}).then((response) => {
response = response.data;
if(response.success){

this.mailBoxList.list[index].is_read = 1;
}else{

console.log("code:1002");
}
});
},
selectMail(mailItem,index) {

if (!mailItem.is_read) {
this.isRead(mailItem.id,index);
}
this.$router.push({ path: '/mailContent', query: { mailId: mailItem.sourceId, dialogueTitle:`与${mailItem.title}进行沟通` }});
},

deleteMessage(mailItem) {
this.sheetShow = true;
this.deleteMessageId = mailItem.id;
},
SheetClick(key) {
if( key === 'menu2') {
this.$http.post(`${WX_URL}base/notice/post/deleteNoticeByUser`,{'noticeId':this.deleteMessageId,'userId':this.userId}).then( (response) => {
response = response.data;
if( response.success ) {
this._initMailBox();
}
});
}
},
fetchData() {
this._initMailBox();
},
},
components: {
'v-header': header,
LingLoading,
emptyTips,
Actionsheet
}
}



